
import React from 'react';

const Cart = (props) => {
    return (
        <div>
            <div className="card">
                <div className="card-body">
                    <h4 className="card-title">Total Item : {props.cart.length}</h4>
                    This is some text within a card body.
                </div>
            </div>
        </div>
    );
};

export default Cart;